* {
  margin: 0;
  top: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to left bottom, #039be5, #01579b);
  font-family: "Teko";
  font-size: 2em;
  overflow: hidden;
}

#root {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.App {
  background: linear-gradient(to right top, rgba(128, 128, 128, 0.966), rgba(78, 78, 78, 0.644));
  margin: 18px;
  width: 440px;
  min-width: 340px;
  min-height: 400px;
  padding: 0 14px;
  border: 1px solid black;
  border-radius: 8px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lives {
  display: flex;
  justify-content: space-around;
  width: 100%;
  padding: 0 8px;
}

.span-lives {
  padding: 10px;
}

.score-num {
  color: rgb(39, 38, 38);
}

h1 {
  color: #fff;
  text-shadow: 3px 3px 2px rgb(0, 0, 0, 0.6);
  text-align: center;
  font-size: 1.2em;
  font-weight: bold;
  margin-top: 10px;
}

.passwd {
  display: flex;
  padding: 0 10px;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-family: monospace;
  gap: 0.7em;
  margin: 5px 5px;
}

h2 {
  color: #fff;
  text-shadow: 2px 2px 2px rgb(0, 0, 0, 0.6);
  text-align: center;
  font-size: 1.1em;
  font-weight: bold;
  margin-top: 10px;
}

.alphabet-hide {
  visibility: hidden;
}

.alphabet {
  visibility: visible;
  display: flex;
  justify-content: center;
  margin: 20px;
  padding: 2px 4px;
  flex-wrap: wrap;
  flex: 0.8;
}

.btn-alphabet:active {
  scale: 0.9;
}

.btn-alphabet {
  margin: 2px;
  border: 1px solid black;
  border-radius: 6px;
  font-family: monospace;
  width: 30px;
  height: 30px;
  font-weight: bold;
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.3);
  align-self: center;
}

.btn-alphabet-quest {
  background-color: #039be5;
  border-radius: 6px;
  margin: 2px;
  border: 1px solid black;
  font-family: monospace;
  width: 30px;
  height: 30px;
  scale: 0.85;
}

.win {
  height: 50px;
  margin-bottom: 10px;
}

.restart-div-hide {
  visibility: hidden;
}

.restart-div {
  width: 100%;
  visibility: visible;
  display: flex;
  justify-content: center;
  padding: 14px 10px;
}

.restart {
  justify-content: center;

  margin: auto 0;
  color: blue;
  font-size: 0.6em;
  padding: 4px 10px;
  border: 1px solid black;
  border-radius: 4px;
  text-align: center;
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.3);
  font-family: monospace;
  transition: box-shadow 0.2s;
  animation: reset 1.4s ease-in-out infinite;
}

.restart:hover {
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.6);
  background: gray;
  color: white;
  animation-play-state: paused;
}

.lastBestScore {
  display: flex;
  justify-content: flex-end;
  padding: 18px;
  font-size: 0.5em;
  color: rgb(32, 30, 30);
  width: 100%;
}

@keyframes reset {
  0% {
    opacity: 0.5;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0.5;
  }
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  span {
    font-size: 0.8em;
  }
}
